import React from 'react';
import{ Link } from 'react-router-dom';

  function Header() {
  return (
    <header style={headerStyle}>
      <h1>ToDo List</h1>
      <a style={LinkStyle} href="/"> Home</a> | <a style={LinkStyle} href ="about">About</a>
    </header>
  )
}

export default Header

const headerStyle = {
    background: '#333',
    color: '#fff',
    textAlign: 'center',
    padding: '10px'
}

const LinkStyle = {
  color: '#fff',
  textDecoration: 'none'
}